body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

.container {
  text-align: center;
  background-color: #e5d0d0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 30%;
  height: 16rem;
  padding: 5px;
  position: absolute;
  bottom: 0;
  right: 2rem;
}

.minContainer {
  background-color: #e5d0d0;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 30%;
  height: 2rem;
  position: absolute;
  bottom: 0;
  right: 2rem;
}

.topBtn {
  flex-direction: row;
  align-items: center;
  height: 10%;
  position: absolute;
  top: 1px;
  right: 10px;
}

.closeBtn {
  color: #4f4e4e;
  margin-left: 5px;
}

.minBtn {
  color: #4f4e4e;
  font-size: 15px;
}

.maxBtn {
  color: #4f4e4e;
  margin-top: 5px;
  font-size: 16px;
}

.content {
  background-color: #e5d0d0;
  flex: 1;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.header {
  height: 20%;
  margin-top: 30px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 24px;
}

.feedback {
  flex-direction: column;
  height: 30%;
  padding: 5px;
}

.nextStep {
  color: #474646;
  font-size: 25px;
}

.inputBox {
  width: 80%;
  height: 7rem;
  margin-top: .2rem;
}

.submitBtn {
  cursor: pointer;
  background-color: #6ef86e;
  border: none;
  border-radius: 10px;
  width: 6rem;
  height: 2rem;
  margin-top: 5px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 15px;
  font-weight: bold;
}

.ratingContainer {
  border-width: 1px;
  border-color: #bcbaba;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.rate {
  color: #424242;
  cursor: pointer;
  border-width: 1px;
  padding: 10px;
  font-size: medium;
}

.selected {
  color: #424242;
  cursor: pointer;
  background-color: #a3a1a1;
  border-width: 1px;
  padding: 10px;
  font-size: medium;
}

